/** Button component that takes in props for link, type, & text.
Renders button w/ given style & props. */

import React from "react";
import { Link } from "react-router-dom";

const Button = ({ props }) => {
    const buttonStyle = {
        color: 'white',
        borderRadius: '50px',
        border: '2px solid transparent'
    };

    return (
        <div>
            {props.link ? (
                <Link to={props.link}>
                    <button style={buttonStyle} className="text-base lg:text-lg px-4 py-1 md:px-6 md:py-1 bg-orange" type={props.type}>{props.text}</button>
                </Link>
            ) : (
                <button style={buttonStyle} className="text-base md:text-lg px-4 py-1 md:px-6 md:py-1 bg-orange" type={props.type}>{props.text}</button>
            )
            }
        </div>
    )
}

export default Button;